@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');


body {
	background: linear-gradient(180deg, rgba(0, 0, 0, 0.932) 0%, rgba(0, 0, 0, 0.774) 100%),
	url('./assets/img/bg.png') 0 0 /cover no-repeat;
	height: 100vh;
	font-family: 'Poppins', sans-serif;
}
.app__container{
	max-width: 980px;
	margin: 0 auto;
	padding: 0px 15px;
}


//========================================================================================================================================================

.header{
	color: rgba(255, 255, 255, 0.884);
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 500px;
	padding: 40px 0px;
	font-size: 24px;
	&__preloader {
		
	}
	&__item {
		background-color: rgba(129, 125, 125, 0.582);
		padding: 10px 20px;
		border-radius: 8px;
		box-shadow: 0 0 5px rgba(255, 255, 255, 0.3);
		min-width: 200px;
		span {
			display: inline-block;
			margin-right: 20px;
		}
	}
}
.header__preloader {
	width: 25px;
	margin: 0  auto;
	img {
		width: 100%;
	}
}

//========================================================================================================================================================
.main {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	row-gap: 10px;
	align-items: center;
	height: 500px;
	padding-top: 40px;
	&__title {
		h1 {
			color: rgba(255, 255, 255, 0.37);
			text-shadow: 0px 0px 5px rgba(185, 181, 181, 0.397);
		}
		p {
			color: rgb(51, 67, 71);
			text-shadow: 0px 0px 5px rgba(255, 250, 250, 0.712);
			background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255,255,255,0.8015581232492998) 48%, rgba(255, 255, 255, 0) 100%);
			padding: 2px 20px;
			border-radius: 10px;
			opacity: 0.5;
			margin-top: -10px;
			margin-bottom: 50px;
		}
	}
}

//========================================================================================================================================================
.converter-row {
	display: flex;
	input {
		padding: 0px 20px;
		background-color: rgba(255, 255, 255, 0.521);
		border-radius: 10px;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
		text-shadow: 0px 0px 5px rgba(112, 110, 110, 0.397);
		border: 1px solid rgb(0, 0, 0);
		font-weight: 700;
		text-align: center;
		width: 100px;
		&:focus {
			outline: none;
		}
	}
	.custom-select__control{
		padding-left: 10px;
		background-color: rgba(255, 255, 255, 0.521);
		border-radius: 10px;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
		text-shadow: 0px 0px 5px rgba(112, 110, 110, 0.397);
		border: 1px solid rgb(0, 0, 0);
		font-weight: 700;
	}
}